import React,{Component} from 'react'
import {Button} from 'antd-mobile'
import './eachMessage.less'
class EachMessage extends Component{
    constructor(props){
        super(props)

    }
    render(){       
         return (
             <div className="eachPart" onClick={this.props.toSeeOccupancy}>
                 <div className="titleBox flexSpace">
                     <span className="titleText flexSpace"><span className={this.props.msgType=='2'?'line secondaryColor':"line"}></span> <span className={this.props.msgType=='2'?'secondaryColor':""}>{this.props.msgType=='1'?'插队申请':"插队反馈"}</span> </span>
                     <span className={this.props.msgType=='2'?'titleText secondaryColor':"titleText"}>会议室：{this.props.roomName}</span>
                 </div>
                 <div className="contentBox">
                     <p><span>{this.props.msgType=='1'?"插队申请":"反馈信息"}</span>：原因原因原因原因原因原因原因原因原因原因原因原因 </p>
                     <p>会议时间：2019-11-09 11：00-12：00 PM</p>
                     <p>地址：11楼东侧</p>
                 </div>
                 {
                     this.props.msgType=='1'?(
                        <div className="btnBox flexCenter">
                        <Button type="primary" className="mainbtn">同意</Button>
                        <Button type="primary" className="mainbtn mainBtnTrans marginLeft">拒绝</Button>
                       </div>
                     ):(
                        <div className="btnBox flexCenter">
                           <Button type="primary" className="mainbtn" style={{visibility:'hidden'}}></Button>
                           <Button type="primary" className="mainbtn secondaryColorbg">标志已读</Button>
                       </div>
                     )
                 }
                 
             </div>
         )
    }
}

export default EachMessage;