import React,{Component} from 'react'
import TopTab from '../../component/topTab/topTab'
import EachMessage from '../../component/eachMessage/eachMessage';
import {WhiteSpace} from 'antd-mobile'

export default class MyMsg extends Component{
    constructor(props){
        super(props);
        this.state={
            tabIndex:0,
            tabs:[{title:'未处理'},{title:'已处理'}]

        }
    }
    // 顶部tab点击，刷新数据
    topTabClick=(tab, index)=>{
        console.log('onTabClick', index, tab)
        this.setState({
            tabIndex:index
        })
    }
    render(){
        return (
            <div className="bgW">
               <TopTab  tabs={this.state.tabs} topTabClick={this.topTabClick} tabContent={
                  <div>
                       <EachMessage  msgType="1" roomName="11楼5室"/>
                       <WhiteSpace size="lg"/>
                       <EachMessage  msgType="2" roomName="11楼5室"/>
                  </div>
               }/>
            </div>
        )
    }

}