import React,{Component} from 'react'
import ListDatePicker from '../../component/listDatePicker/listDatePicker'
import EachMeeting from '../../component/eachMeeting/eachMeeting'
import {WhiteSpace,Flex,Button} from 'antd-mobile'
import './meetingRoomDetail.less'
import {formatTime,getOperatingSystem} from  '../../assets/js/common'
const nowTimeStamp = Date.now();
const now = new Date(nowTimeStamp);
class MeetingRoomDetail extends Component{
    constructor(props){
        super(props)
        this.state={
            path:'/meetingRoomDetail',
            date:now
        }
        
    }
     // 跳转插队申请
     queueJumping=()=>{
        this.props.history.push(
            {pathname:'queueJumping'}
        )
    }
    // 顶部时间改变
    dateChange=(date)=>{
        
        this.setState({
            date
        })
    }
   
    componentWillMount(){
        // console.log(formatTime)
    }
    getCurrentPath(name){
        
    }
    render(){
        return (
            <div className="meetingRoomDetail">
                 {/* 顶部日期选择 */}
                 <div className="datePickerBox">
                    <Flex justify="between">
                        <Flex justify="center">
                            <i className="iconfont icon-riqi"></i>
                            <Flex.Item>
                                <ListDatePicker formatTime={val => formatTime(val).split(' ')[0]} arrow="empty" date={this.state.date} title={this.state.startTimeTitle}  mode="date" onChange={this.dateChange}/>
                            </Flex.Item>
                        </Flex>
                        <Flex justify="center">
                            <Button type="primary" size="small" style={{background:'#2dbaac',borderColor:'#2dbaac'}} inline><i className="iconfont icon-add activeColor"></i></Button>
                        </Flex>
                    </Flex>
                </div>
                <WhiteSpace size="sm"/>
                {/* 循环每一个会议 */}
                <EachMeeting meetingStatus='1' />
                <WhiteSpace size="sm"/>
                <EachMeeting meetingStatus='2'/>
                <WhiteSpace size="sm"/>
                <EachMeeting meetingStatus='3' queueJumping={this.queueJumping}/>
            </div>
        )
    }
}
export default MeetingRoomDetail;